.app {
  padding: 0;
}

.page {
  margin: 10px;
}

.question-answers {
  display: grid;
  grid-auto-flow: row;
  grid-template-columns: repeat(3, 1fr);
  justify-content: stretch;
  align-items: stretch;
  flex-wrap: wrap;
  margin: 2.5px;
}

.answer-text {
  width: 15em;
}

.answer {
  border: 1px solid black;
  margin: 2.5px;
  height: 6em;
  display: flex;
  align-items: center;
  justify-content: center;
}

sub {
  line-height: 0;
}

h1 {
  text-align: center;
}

@media (max-width: 700px) {
  .question-answers {
    grid-template-columns: repeat(2, auto);
  }

  .answer-text {
    width: 10em;
    max-width: 35vw;
  }
}

/* tabs */
.tab-bar {
  display: flex;
  align-items: center;
  justify-content: stretch;
  position: sticky;
  top: 0;
}

button.tab {
  border-radius: 0;
  flex-grow: 1;
}

button.tab[aria-pressed="true"] {
  border-style: inset;
}

.answered-question-bar {
  width: 100%;
  display: grid;
  grid-template-columns: auto auto auto;
  align-items: center;
  justify-content: space-between;
}

.question-icon {
  width: 1em;
}
